let paso = 1;

document.addEventListener('DOMContentLoaded', function(){

    iniciarApp();

});

function iniciarApp(){
    addEventListener();
    darkMode();
    mostrarSeccion();
    tabs();
    
}

function mostrarSeccion(){
    //ocultar la seccion
    const seccionAnterior = document.querySelector('.mostrarSeccion');
    if(seccionAnterior){
        seccionAnterior.classList.remove('mostrarSeccion');
        seccionAnterior.classList.add('secciones');
    }

    //muestra la seccion seleccionada
    const pasoSelect = `#paso-${paso}`; 
    const seccion = document.querySelector(pasoSelect);

    seccion.classList.add('mostrarSeccion')
    seccion.classList.remove('secciones');

    //ocultar tabs 
    const tabAnterior = document.querySelector('.actual');

    if(tabAnterior){
        tabAnterior.classList.remove('actual');
    }

    const tab = document.querySelector(`[data-paso="${paso}"]`);

    tab.classList.add('actual')

}
function tabs(){

    const botones = document.querySelectorAll('.tabs button');

    botones.forEach(boton =>{
        boton.addEventListener('click', (e)=>{
        paso = parseInt(e.target.dataset.paso)

        mostrarSeccion();
  
        })
    })
}






function addEventListener(){

    const menuMobile = document.querySelector('.mobile_menu');

    menuMobile.addEventListener('click', navegacionResponsive);

    //mostrar forma de contacto condicional

    const metodoContacto = document.querySelectorAll('input[name="contacto[contacto]"]');

    //iteramos con un foreach por que para aÃ±adir en eventListener no funciona con un queryselectorAll
    //pero podemos hacerlo con el foreach
    metodoContacto.forEach( input => input.addEventListener('click', mostrarMetodosContacto))

}

function mostrarMetodosContacto(e){

    const contacto = document.querySelector('#contacto');

    if(e.target.value === "telefono"){
        contacto.innerHTML = `<label for="telefono">Su numero</label> 
        <input type="tel" placeholder="Tu telefono"  id="telefono" name="contacto[telefono]">

        <p>Indique cuando desea ser contactado</p>
        <label for="fecha">Fecha</label>
        <input data-cy="input-fecha" type="date" id="fecha" name="contacto[fecha]">
        <label for="hora">Hora</label> 
        <input data-cy="input-hora" type="time"  id="hora" min="09:00" max="18:00" name="contacto[hora]">
        
        `;
    }else{
        contacto.innerHTML =`<label for="email">E-Mail</label> 
        <input type="email" placeholder="Tu Email"  id="email" name="contacto[email]" required>`;
    }
}

function navegacionResponsive(){
    const navegacion = document.querySelector('.navegacion');

    /*
    if(navegacion.classList.contains('mostrar')){
        navegacion.classList.remove('mostrar')
    }else{ navegacion.classList.add('mostrar') }
*/
    navegacion.classList.toggle('mostrar');//lo  mismo de arriba pero menos codigo
}


function darkMode(){

     //revisa si la preferencia del sistema es tema oscuro o claro
    const preferencia = window.matchMedia('(prefers-color-scheme: dark)');

    if(preferencia.matches){
        document.body.classList.add('dark_mode');
    }else{
        document.body.classList.remove('dark_mode');
    }

    preferencia.addEventListener('change',function(){

        if(preferencia.matches){
            document.body.classList.add('dark_mode');
        }else{
            document.body.classList.remove('dark_mode');
        }
    })

    const btndarkMode = document.querySelector('.dark-mode');

    btndarkMode.addEventListener('click', function(){
       document.body.classList.toggle('dark_mode');
    });

   
}

